<script>
    export let yScale;
    export let width;
    export let height;

    $: ticks = [0,0.5,1,1.5,2,2.5,3,3.5];


</script>

<g class= "axis-y">
        {#each ticks as tick, index}
            <g class="tick" transform="translate(0, {yScale(tick)})">
                <text x=5 y=-6>+{tick}ºC</text>
                <line x1=5 x2={width} y1=0 y2=0 stroke={tick === 1.5 ? "black" : "#E2E2E2"}></line>
            </g>

        {/each}
        <text class="axis-title" x=5 y={10}>Warm since 1960 &darr;</text>
        <text class="axis-subtitle" x=5 y={28}>The circle is sized according to the country's population</text> 
</g>

<style>
    .axis-title {
        font-weight: 600;
        font-size: 14px;
    }

    .axis-subtitle {
        font-weight: 200;
        font-size: 13px;
        color: grey;
    }

    .axis-y {
        font-size: 12px;
    }



</style>