<script>
    export let data;
    export let colorScale;
    console.log({data});

</script>

<div class="tooltip" 
     style="position: absolute;
     top: {data.y + 20}px;
     left: {data.x + 20}px">
     <h1 class="tooltip-h1">{data.country}</h1>
     <h2 class="tooltip-h2">{data.region}</h2>
     <div class="info">
        <span class="warm" style="background:{colorScale(data.warm_since_1960)};color:{data.warm_since_1960 >1.5 ? "white" : "black"}">+{data.warm_since_1960}ºC</span>
        <span class="deviation">(±{data.deviation} deviation)</span>
    </div>
</div>


<style>
    .tooltip {
        background: white;
        box-shadow: 2px 3px 8px rgba(0,0,0, 0.15);
        padding: 8px 6px;
        border-radius: 3px;
        pointer-events: none;
    }

    .tooltip-h1 {
        font-size: 18px;
        font-weight: 500;

    }

    .tooltip-h2 {
        color: grey;
        text-transform: uppercase;
        font-style: italic;
        font-size: 11px;
        font-weight: 200;
    }

    
    .info {
        display: flex;
        justify-content: space-between;
        column-gap: 8px;
        align-items: center;
    }

    .warm {
        font-size: 14px;
        font-weight: 500;
        padding: 3px;
        border-radius: 4px;

    }

    .deviation {
        font-size: 14px;
        font-weight: 300;
    }
</style>
