<script>
	
	import data from "./data/data.js";
	import { mean , rollups } from "d3-array";
	import { forceSimulation,
		forceX,
		forceY,
		forceCollide
	} from "d3-force";
	import { scaleLinear, scaleBand, scaleSqrt, scaleOrdinal, scaleThreshold } from "d3-scale";
	import AxisY from "./components/AxisY.svelte";
	import Tooltip from "./components/Tooltip.svelte";
	import Annotations from "./components/Annotations.svelte";
	

	let width = 400;
    let height = 1200;

    const margin = {
      top: 20,
      right: 0,
      bottom: 30,
      left: 0
    }

	$: innerWidth = width - margin.left - margin.right;
    let innerHeight = height - margin.top - margin.bottom;

    const RADIUS = 5;

	 // y scale definition
	 $: yScale = scaleLinear()
      .domain([0.3,3.65])
      .range([0,innerHeight])
    

    //Size scale
    const sizeScale = scaleSqrt()
      .domain([0, 1500000000])
      .range([2, 45]); // Adjust range as needed for desired circle sizes

    //Add color to each circle
    // const colorRange = ["#bde0fe", "#fb8500"];

    // const colorScale = scaleOrdinal()
    //     .domain("lower", "higher")
    //     .range(colorRange) // Colors for lower and higher values

    // Color scale with thresholds
  const colorScale = scaleThreshold()
    .domain([1,1.5, 2, 2.5, 3,3.5])
    .range(["#e7d7c1","#e7d7c1", "#e5383b","#ba181b", "#a4161a", "#660708"]); // Colors for each range

    // ["#fae6d5","#ffcd96", "#f4845f","#f25c54", "#bc4b51", "#720026"]
    // new variable to call in the {#each}
  // Reactivity of yScale
  let nodes = [];
  let simulation = forceSimulation(data);

  $: {

  simulation
    .force("x",  forceX().x(width / 2).strength(0.1))
    .force("y",  forceY().y(d => yScale(d.warm_since_1960)).strength(0.8))
    .force("collide", forceCollide().radius(d => sizeScale(d.population) +1))
    .alpha(.3)
    .alphaDecay(.02)
    .restart();
  }

  simulation.on("tick", () => {
    nodes = simulation.nodes();
  });

  console.log(nodes);

  let hovered;

</script>

<div class="chart-container" bind:clientWidth={width}>
	<svg {width} {height}>
	  <g class="inner-chart" transform="translate({margin.left}, {margin.top})">
		  
		<AxisY {yScale} height={innerHeight} width ={innerWidth}/>
		{#each nodes as node}

			<!-- svelte-ignore a11y-no-noninteractive-tabindex -->
			<circle
				cx={node.x}
				cy={node.y}
				r={sizeScale(node.population)}
				fill={colorScale(node.warm_since_1960)}
				opacity={hovered ? hovered === node ? 1 :0.5 :1}
				on:mouseover={() => {
				  hovered = node;
				}}
				on:focus={() => {
				  hovered = node;
				}}
				on:mouseleave={() => {
				  hovered = null;
				}}>

			</circle>

		  {/each}
		  </g>
		  <Annotations width={innerWidth} height={innerHeight}/>
	  
	
	</svg>
	{#if hovered}
	  <Tooltip data={hovered} {colorScale}/>
	{/if}
	<h3>Source: Berkeley Earth (data accessed on 2024-07-15).</h3>
	
</div>


<style>

    h3 {
      font-size: 14px;
      color: #9c9b9b;
	    font-weight: 300;
      padding: 10px;
    }

    .chart-container {
      max-width: 700px;
      align-items: center;
      margin: auto;
      padding: 10px; /* Add padding to the chart container */
    }

    circle {
      transition : opacity 500ms ease;
      cursor: pointer;
    }

    :global(p, h1, h2, h3, text, span) {
      color: #333;
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
    }
  </style>


